import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class UsersService {

  // baseURL: any = 'http://english.evaarthalu.com/startupyo/index.php/';
  // baseURL: any = 'http://localhost/startupyo/';
  // baseURL: any = 'http://talk.startupyo.com/api/';
  baseURL: any = 'https://startupyo.com/api/';

  constructor(private httpClient:HttpClient) { }

  postData(url,data): Promise<any> {
    let postdata = JSON.stringify(data);
    console.log(postdata);
    return this.httpClient.post(this.baseURL+url, postdata)
        .toPromise()
        .then(response => {
            return response;
        })
        .catch(err => {
          return {
            'type': 'fail',
            'message': err.statusText
          }
        });
  }

}