import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { UsersService } from '../users.service';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.css']
})
export class DetailsComponent implements OnInit {

  trainingData: any = [];

  constructor(private title: Title, private meta: Meta, private usersService:UsersService) { }

  ngOnInit() {
    this.getTrainingDetails()
  }

  getTrainingDetails(){
    this.usersService.postData('home-training-details',{trainingId: '21'})
      .then(res => {
        if(res){
          if(res.type != 'success'){
            this.trainingData = '';
          }else{
            this.trainingData = res.trainings_data;

            if(res.trainings_data.meta_title){
              this.title.setTitle(res.trainings_data.meta_title);
              this.meta.updateTag({ name: 'keywords', content: res.trainings_data.meta_title })
            }
          }
        }
      },
      err => {
      });
  }

}
